import { block } from "bem-cn";
import useSenderWallet from "lib/near/sender-wallet";
import useTrustWallet from "lib/near/trust-wallet";
import { environment } from "lib/config";

import { GeneralLayout, Loader, LoaderContainer } from "ui";
import { Agreement } from "components/agreement";
import { PayWidget } from "components/pay-widget";
import { useNearWallet } from "lib/near/near-wallet";
import { useNear } from "lib/near";
import "./payment-page.scss";
import { usePayment } from "models/payment";

const b = block("pay-with-near-payment-page");

export function PaymentPage() {
  const near = useNear();
  const { payment, isExpired, handleExpired } = usePayment();
  const nearWallet = useNearWallet({ near, payment: payment ?? null });
  const senderWallet = useSenderWallet();
  const trustWallet = useTrustWallet();

  const isLoaded = (!!payment || isExpired) && near.isReady;
  const backUrl = payment?.return_url || document.referrer || environment.payWithNearUrl;

  if (!isLoaded) {
    return (
      <GeneralLayout>
        <LoaderContainer>
          <Loader />
        </LoaderContainer>
      </GeneralLayout>
    );
  }

  if (isExpired) {
    return (
      <GeneralLayout className={b()}>
        <span className={b("message")}>This payment link is expired</span>
        {document.referrer && (
          <a className={b("link")} href={backUrl}>
            Back to shop
          </a>
        )}
      </GeneralLayout>
    );
  }

  if (!payment) {
    return null;
  }

  return (
    <GeneralLayout>
      <PayWidget
        payment={payment}
        hasTrustWallet={trustWallet.isAvailable}
        hasSenderWallet={senderWallet.isAvailable}
        onTrustWallet={(cur) => trustWallet.pay(cur)}
        onSenderWallet={(cur) => senderWallet.pay(cur, payment)}
        onNearWallet={(cur) => nearWallet(cur)}
        onExpired={handleExpired}
      />
      <Agreement />
    </GeneralLayout>
  );
}
